body {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.score {
  text-align: center;
}

.game {
    max-width: 600px;
    margin: 0 auto;
  }
  
  .body {
    text-align: center;
    position: relative;
    background-color: #efefef;
    border: thin solid #ddd;
  }
  
  .meaning, .ink {
    font-size: 350%;
    font-weight: bold;
    margin: 10px;
  }
  
  .help {
    color: #666;
    margin: 10px;
    text-align: center;
  }
  
  .buttons button {
    margin: 20px;
    font-size: 200%;
    padding: 10px 20px;
    background-color: #fff;
    border: thin solid #ccc;  
  }
  
  button:focus {
    outline: none;
  }
  
  .game-status {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0.85;
    background-color: #eee;
    font-size: 125px;
    font-weight: bold;
  }
  
  .status-playing {
    display: none;
  }
  
  .status-correct:after {
    content: "\2713";
    color: green;
  }
  
  .status-wrong:after {
    content: "\2717";
    color: red;
  }


